header {
    position: fixed;
    height: 80px;
    width: 100%;
    top: 0;
    -webkit-transition: all .5s ease-out 0.1s;
    -moz-transition: all .5s ease-out 0.1s;
    -o-transition: all .5s ease-out 0.1s;
    transition: all .5s ease-out 0.1s;
    z-index: 1;
    background-color: rgba(12, 9, 4, 0.9);
}
.nav-up {
    background-color: rgba(12, 9, 4, 0);
}
header > img {
    padding-left: 50px;
    width: 80px;
    height: 80px;
    float: left;
}
header > a {
    position: absolute;
    right: 100px;
    top: 10px;
    color: white;
    text-decoration: none;
    font-weight: bold;
    font-size: 20px;
    margin: 10px;
}
